import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

type Framework = { id: string; key: string; name: string; description?: string }
type Role = "ADMIN" | "REVIEWER" | "EXECUTOR"
type AppUser = { id: string; name: string; email: string; role: Role; area?: string; isActive: boolean }

const MOCK_FRAMEWORKS: Framework[] = [
  { id: "fw-1", key: "SOX", name: "SOX (IT Controls)", description: "Controles IT para auditoria financeira." },
  { id: "fw-2", key: "SOC", name: "SOC 2", description: "Controles de segurança e disponibilidade." },
  { id: "fw-3", key: "ISO", name: "ISO 27001", description: "ISMS e controles de segurança." },
  { id: "fw-4", key: "PCI", name: "PCI DSS", description: "Controles para ambiente de cartões." },
]

const MOCK_USERS: AppUser[] = [
  { id: "u-1", name: "Ricardo Santos", email: "ricardo@empresa.com", role: "REVIEWER", area: "Security", isActive: true },
  { id: "u-2", name: "João Mendes", email: "joao@empresa.com", role: "EXECUTOR", area: "TI Infra", isActive: true },
  { id: "u-3", name: "Fillipe Nunes", email: "fillipe@empresa.com", role: "EXECUTOR", area: "Security", isActive: true },
]

function roleBadge(r: Role) {
  const map: Record<Role, { className: string; label: string }> = {
    ADMIN: { className: "bg-primary/10 text-primary", label: "Admin" },
    REVIEWER: { className: "bg-emerald-100 text-emerald-900", label: "Revisor" },
    EXECUTOR: { className: "bg-muted text-foreground", label: "Executor" },
  }
  const cfg = map[r]
  return <Badge className={cfg.className}>{cfg.label}</Badge>
}

